
<template>
    
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ManageExpense') }}</span>
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" title="Page Tour"
                                @click="setTourGuideSteps(0)">
                                <em class="fa fa-road" aria-hidden="true"></em>
                                <br />{{ $t('PageTour') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <Form v-slot="{ valid,errors }" ref="Expenseform">
                <div class="row">
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="Title">Title</label><span class="text-danger">*</span>
                                <Field  name="Title" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                <input  name="Title"  type="text" v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                 v-model="Expense.Title" />
                <span v-if="errors[0]" name="Title" class="invalid-feedback">{{ errors[0] }}</span>
            </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="ExpenseDate">Date</label><span class="text-danger">*</span>
                                <Field name="Expense Date" :rules="{ 'required': true }" class="w-100" v-slot="{ errors, changed }">
           <v-date-picker :popover="popover"  :min-date="getMinDate()" :minute-increment="5" style="flex-wrap:nowrap;" class="input-group w-100" 
           v-model="Expense.ExpenseDate" @update:modelValue="changed">        
               <template v-slot="{ inputValue, inputEvents, togglePopover }">           
                   <input v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }" :value="inputValue" v-on="inputEvents"/>
                   <div @click="togglePopover()" class="input-group-append"><span class="input-group-text">
                       <em  class="fa fa-calendar"></em></span></div>
               </template>
           </v-date-picker>
           <span  v-if="errors[0]" name="Expense Date" class="invalid-feedback d-block">{{ errors[0] }}</span>
       </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="Project">Project</label><span class="text-danger">*</span>
                                 <Field :rules="{ 'required': true }" class="w-100" name="Project" v-slot="{ errors }">      
                                <select name="ProjectId" v-model="Expense.ProjectId" v-bind:class="{ 'form-control w-100': true }"
                                @change="onChangeDropdown">
                                <option value="">Select</option>
                                <option v-for="(item, itemIndex) in ProjectData" :key="itemIndex" :value="item.value">{{ item.name }}</option>
                                </select>
                                <span vif="errors[0]" name="Project" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                            </div>
                        </div> 
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="Trip">Trip Name</label>                               
                                <div class="input-group">                          
                                <Field :rules="{ 'required': false }" class="w-100" name="Trip" v-slot="{ errors }">      
                                <select name="TripId" v-model="Expense.TripId" v-bind:class="{ 'form-control w-100': true }">
                                <option value="">Select</option>
                                <option v-for="(item, itemIndex) in TripData" :key="itemIndex" :value="item.TRIP_ID">{{ item.TRIP_NAME }}</option>
                                </select>
                                <span vif="errors[0]" name="Trip" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                                    <div class="input-group-append">
                                            <div class="ml-2" v-if="UserType == 'CA'">
                                                <a class="aAddMorePopUp submitbtn btn-dark theme-primary round-icon-small" href="javascript:;" popuparea="Expense" popupcontroller="Trip"
                                                   popupopenaction="OpenTripPopUp" popupsaveaction="SaveTripPopUp" relatedddl="TripId"
                                                   relatedddlcontroller="Trip" tabindex="5" relatedddlaction="FillTripDropDownList" popuptitle="@Resources.AddTrip">
                                                    <i class="fa fa-plus pt-7"></i>
                                                </a>
                                            </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="Category">Category</label>
                                <Field :rules="{ 'required': true }" class="w-100" name="Category" v-slot="{ errors }">      
                                <select name="CategoryId" v-model="Expense.CategoryId" v-bind:class="{ 'form-control w-100': true }">
                                <option value="">Select</option>
                                <option v-for="(item, itemIndex) in CategoryDataddl" :key="itemIndex" :value="item.EXPENSE_CATEGORY_ID">{{ item.CATEGORY_NAME }}</option>
                                </select>
                                <span vif="errors[0]" name="Category" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="Task">Task</label>
                                <Field :rules="{'required': false}" class="w-100" name="Task" v-slot="{ errors }">      
                                <select name="TaskId" v-model="Expense.TaskId" v-bind:class="{ 'form-control w-100': true }">
                                <option value="">Select</option>
                                <option v-for="(item, itemIndex) in TaskDataddl" :key="itemIndex" :value="item.task_id">{{ item.task_name }}</option>
                                </select>
                                <span vif="errors[0]" name="Task" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label><label for="Amount">Amount</label>&nbsp;<span id="getCurrency">{{ CurrencyCode }}</span>:<span class="text-danger">*</span></label>                            
                                <Field  name="Amount" class="w-100" :rules="{ 'required': true,'max':11 }" v-slot="{ errors }">
                    <input  name="ExpenseAmount"  type="text" v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                     v-model="Expense.ExpenseAmount" />
                    <span v-if="errors[0]" name="Amount" class="invalid-feedback">{{ errors[0] }}</span>
                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group"> 
                                <label for="Currency">Currency</label><span class="text-danger">*</span>                             
                                <Field :rules="{ 'required': true }" class="w-100" name="Currency" v-slot="{ errors }">      
                                <select name="Currency" v-model="Expense.CurrencyId" v-bind:class="{ 'form-control w-100': true }"
                                @change="OnChangeCurrency">
                                <option value="">Select</option>
                                <option v-for="(item, itemIndex) in CurrencyNameddl" :key="itemIndex" :value="item.CurrencyId">{{ item.Currency }}</option>
                                </select>
                                <span vif="errors[0]" name="Currency" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                            </div>
                        </div>
                        <div class="col-lg-3">
                            <div class="form-group">
                                <label for="TagIds">Tag</label>
                                <div class="row">
                                    <div class="col-12 float-left">
                                        <div class="input-group multiselect-group">
                                             <Field :rules="{ 'required': false }" class="w-100" name="Tag" v-slot="{ errors }">      
                                <select name="Tag" v-model="Expense.TagIds" v-bind:class="{ 'form-control w-100': true }">
                                <option value="">Select</option>
                                <option v-for="(item, itemIndex) in TagDataddl" :key="itemIndex" :value="item.id">{{ item.name }}</option>
                                </select>
                                <span vif="errors[0]" name="Tag" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                                            <div class="input-group-append">
                                                    <div class="ml-2" v-if="UserType == 'CA'">
                                                        <a class="aAddMorePopUp submitbtn btn-dark theme-primary round-icon-small" href="javascript:;" 
                                                        popupcontroller="Tag" popupopenaction="openAddTagPopup"
                                                           popupsaveaction="SaveTagPopUp" relatedddl="ddlTagList"
                                                           relatedddlcontroller="Tag" relatedddlaction="FillTagDropDownList"
                                                           popuptitle="@Resources.Tag" multiselect="yes">
                                                            <i class="fa fa-plus pt-7"></i>
                                                        </a>
                                                    </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <div class="col-lg-12 mt-3 mb-3">
                            <div class="heading-border">
                                <h5 class="h5">
                                    <!-- data-content="@Html.Raw(TALYGEN.Models.CommonClass.GetMaximumFileSizeMessage(loggedUserInfo.CompanyId, loggedUserInfo.ID,"Expenses"))" -->
                                    <span>{{$t('Attachment')}}</span> <a asp-custom-tabindex="10" href="javascript:;" data-toggle="popoveruserguide">
                                        <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                    </a>
                                </h5>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                            <div class="col-lg-4 float-left">
                                <div class="form-group mb-1">
                                    <div class="input-group">
                                        <div class="custom-file">
                                            <input  type="file" name="FileSrc" id="flFile" data-file="multi" tabindex="-1" @change="setFile($event)" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                            <div class="bootstrap-filestyle input-group">
                                                <input type="text" class="form-control disabled" disabled="" v-model="attachmentDiv" /> 
                                                <div class="input-group-append" style="cursor:pointer;">
                                                    <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                                        <label title="Click here to select a file." for="flFile" class="mb-0 font-weight-normal ">Choose File</label>
                                                    </span>
                                                    <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                        <a href="javascript:;" class="round-icon-small btn-danger-light" title="Clear" @click="Clear()"><i class="fa fa-undo"></i></a>
                                                        <a href="javascript:;" class="round-icon-small btn-success-light" id="addmailBlackListed" @click="Addmore()" title="Add"><i class="fa fa-plus pt-7"></i></a> 
                                                        <a href="javascript:;" class="round-icon-small btn-danger-light d-none" title="Remove" @click="Remove()"><i class="fa fa-times"></i></a>
                                                        <a  v-if="fileCount>0" href="javascript:;" class="round-icon-small btn-primary-light" title="Remove" @click="ViewFile()"><i class="fa fa-eye"></i></a>                                                                                                            
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>  
                            <div class="col-lg-4 float-left" v-for="(item, index) in Allattachment">
	                            <div class="form-group mb-1" :id="'seconddiv'+index">
	                            	<div class="input-group">
	                            	<div class="custom-file">
	                            		<input @change="setFile($event)" type="file" :name="'FileSrc'+index" :id="'flFile'+index" data-file="multi" tabindex="-1"  style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
	                            		<div class="bootstrap-filestyle input-group">
	                            			<input type="text" class="form-control disabled" disabled="" :value="secondattachment[index]" />
	                            				<div class="input-group-append" style="cursor:pointer;">
	                            					<span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
	                            						<label title="Click here to select a file." :for="'flFile'+index" class="mb-0 font-weight-normal ">Choose File</label>
	                            					</span>
	                            					<span class="input-group-text border-0 bg-transparent p-0 pl-2">
	                            						<a href="javascript:;" class="round-icon-small btn-danger-light remove" title="Remove" @click="Remove(index)">
	                            							<i class="fa fa-times"></i>
	                            						</a>
	                            					</span>
	                            				</div>
	                            		</div>
	                            	</div>
	                            </div>
                            </div>
                        </div>       
                        </div>
                        <div class="col-lg-12">
                            <div class="form-group">                            
                                <Field class="w-100" name="Description" :rules="{ 'required': false, 'max': 500 }" v-slot="{ errors }">
                                <textarea cols="5" v-model="Expense.Description" v-bind:class="{ 'form-control w-100': true }"/>
                                <span v-if="errors[0]" name="Description" class="invalid-feedback">{{ errors[0] }}</span>
                                </Field>
                            </div>
                        </div>
                    </div>
                    <div class="row flex-row-reverse">
                        <div class="col-lg-6 text-right">                            
                                <a v-if="chkStatus == 1096" href="javascript:;" asp-custom-tabindex="14" id="aSubmit" v-on:click="SubmitData(valid,errors, 'D')"
                                class="btn btn-warning formbtn text-white disabled mr-1" :title="$t('SaveAsDraft')"><i class="fa fa-save pr-2"></i>
                                <span>{{$t('SaveAsDraft')}}</span></a>
                               <a v-else href="javascript:;" asp-custom-tabindex="15" id="aSubmit" class="btn btn-warning formbtn text-white mr-1" 
                               v-on:click="SubmitData(valid,errors, 'D')"
                               :title="$t('SaveAsDraft')"><i class="fa fa-save pr-2"></i><span>{{$t('SaveAsDraft')}}</span></a>                            
                            <a href="javascript:;" asp-custom-tabindex="16" id="aSendForApproval" class="btn btn-success formbtn text-white mr-1"
                             :title="$t('SaveandSendforApproval')" v-on:click="SubmitData(valid,errors, 'A')"><i class="fa fa-paper-plane pr-2"></i><span>{{$t('SaveandSendforApproval')}}</span></a>
                            <a href="javascript:;" @click="onCancel()" asp-custom-tabindex="17" :title="$t('Cancel')" class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{$t('Cancel')}}</span></a>
                        </div>
                        <div class="col-lg-6">
                            <div class="py-2">
                                
                            </div>
                        </div>
                    </div>
            </Form> 
            <!-- <ExpenseAttachents v-if="showFileModal" @CloseExpenseAttachments='CloseExpenseAttachments'
                v-bind:expenseid="expid" v-bind:code="statuscode"></ExpenseAttachents>
            <ModelTrip v-if="ShowTripModel" @CloseExpenseTrip='CloseExpenseTrip'></ModelTrip>
            <ModelTag v-if="ShowTagModel" @CloseExpenseTag='CloseExpenseTag'></ModelTag> -->
        </div>
    </section>
</template>

<script>
import ExpenseDataService from "@/services/DataService";
// import ModelTrip from "../../../components/Common/Model/modalTrip.vue"
// import ModelTag from "../../../components/Common/Model/ModelTag.vue"
// import ExpenseAttachents from "../../../components/Common/Model/ExpenseAttachments.vue"
import DynamicForm from "@/components/Common/DynamicForm.vue";
export default {
    components: {
        DynamicForm
    }, 
    data() {
        return {
            isLoading: false,
            UserType:null,
            showFileModal: false,
            ShowTripModel: false,
            ShowTagModel: false,
            CategoryDataddl: [],
            TaskDataddl: [],
            CurrencyNameddl: [],
            CurrencyCode:'',
            TagDataddl: [],
            ExpenseId: 0,
            ProjectData: '',
            IsSendForApproval: null,
            attachmentDiv:'',
            Allattachment:[] ,
            attachment:[],
            secondattachment: [],
            counter:0  ,
            file:[],
            fileCount: '',
            expid: '',
            txtAmountCode: '',
            Currency: null,
            chkStatus: '',
            TripData: [],
            popover: {
                visibility: 'click'
            },
            Expense:{
                Title:'',
                ExpenseDate:new Date(),
                ProjectId:'',
                TripId:'',
                CategoryId:'',
                TaskId:'',
                TagIds:'',
                ExpenseAmount:'',
                CurrencyId:'',
                Description:''
            }
        }

    },
    created:async function () {
        this.UserType = this.GetUserInfo.UserType;
        this.Currency = this.GetUserInfo.Currency.replace('(', '').replace(')', '');               
        console.log(this.UserType)
        await  this.GetProjectDDL();
        await  this.GetTripNameDDL();
        await this.GetCategoryNameDDL();
        await this.GetCurrencyNameDDL();
        await this.GetTagNameDDL();
        await this.FetchData();
    },   
    methods: {
        AddFileMore:function(e, field){
            var vm=this;            
            (vm.FormSchema)[1].Data.push({
                            astype: "FileField",                         
                            showLabel:false,
                            name: "ExpenseAttechment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: (vm.FormSchema)[1].Data.length,
                            config: {
                                onChange: vm.setFile,
                                showAddIcon: true,
                                onAddButtonClick:vm.RemoveFile,
                                addIconBackground:'bg-danger',
                                addIcon:'fa fa-times'
                            }});
        },
        RemoveFile:function(e,field){            
            var vm =this;
            var checkindex =(vm.FormSchema)[1].Data.findIndex(x=>x.FieldIndex === field.FieldIndex);
            if(checkindex > -1){
                (vm.FormSchema)[1].Data.splice(checkindex,1);
            }
        },
        OpenPopup: function (e, field) {
            if (field.name == 'ExpenseTrip') {
                this.ShowTripModel = true;
            }
            else {
                this.ShowTagModel = true;
            }
        },
        onChangeDropdown:async function () {
            let projectId=this.Expense.ProjectId
           await this.GetTaskDDL(projectId);
           await this.GetCurrencyCode(projectId);
        },      
        ViewFile: function () {
            this.showFileModal = true;
            this.expid = this.$route.params.id;
        },
        CloseExpenseAttachments: function () {
            this.showFileModal = false;
        },
        onCancel: function () {            
            this.$router.push({ name: 'Expense' });
        },
        FetchData: function (e) {            
            var vm = this;
            vm.isLoading=true;
            const params = `ExpenseId=${vm.$route.params.id}`;
            ExpenseDataService.GetExpenseByExpenseId(params).then((response) => {
                vm.isLoading=false;
                if (response.data.length > 0) {                    
                    vm.Expense.Title = response.data[0].EXPENSE_TITLE;
                    vm.Expense.ExpenseDate = response.data[0].EXPENSE_DATE;
                    vm.Expense.ProjectId=response.data[0].PROJECT_ID;
                    vm.Expense.TripId= response.data[0].TRIP_ID;
                    vm.Expense.CategoryId = response.data[0].EXPENSE_CATEGORY_ID;    
                    vm.Expense.TaskId=response.data[0].TASK_ID;
                    //vm.GetTaskDDL();
                    vm.Expense.ExpenseAmount = response.data[0].EXPENSE_AMOUNT;
                    vm.Expense.CurrencyId=response.data[0].currency_id;
                    vm.Expense.Description = response.data[0].EXPENSE_DESCRIPTION;
                    vm.fileCount = response.data[0].FILE_COUNT;
                    vm.chkStatus = response.data[0].STATUS_ID;
                }
            });
        },
        setFile:function(e){                         
            if(this.attachmentDiv.length != ''){                
                this.secondattachment.push(e.target.files[0].name); 
                this.file.push(e.target.files[0]);
            } 
            else{                
                this.attachmentDiv = e.target.files[0].name;
                this.file.push(e.target.files[0]);
            }        
        },
        Addmore: function () {             
            this.Allattachment.push(this.attachmentDiv)  
            this.counter++;             
        }, 
        Clear:function(){            
            this.attachmentDiv='';
        }, 
        Remove:function(index){            
            this.Allattachment.splice(index,1)
        },
        SubmitData: function (values, meta, val) { 
            debugger; 
            var vm = this;
            var jsonobj = {
                ExpenseId: vm.ExpenseId == undefined ? 0 : vm.$route.params.id,
                ExpenseTitle: vm.Expense.Title,
                TripId: vm.Expense.TripId,
                ProjectId: vm.Expense.ProjectId,
                ExpDesc: vm.Expense.Description,
                ExpAmount: vm.Expense.ExpenseAmount,
                CategoryId: vm.Expense.CategoryId,
                TaskId: vm.Expense.TaskId,
                ExpAttachment: '',
                IsBulkUpload: 0,
                CurrencyId: vm.Expense.CurrencyId,
                ExpDate: vm.Expense.ExpenseDate,
                IsSendForApproval: val == 'A' ? 1 : 0
            };
            const formData = new FormData();
            formData.append("postString", JSON.stringify(jsonobj));             
              formData.append("files",vm.file);
              for (var i = 0; i < vm.file.length; i++ ){
                    let file = vm.file[i];
                    formData.append('files[' + i + ']', file);
            }           
            vm.$refs.Expenseform.validate().then((result) => {
                // if (typeof vm.ProjectData != 'string' && typeof vm.CurrencyName != 'string') {
                //     result = true;
                // }
                if (result) {
                    ExpenseDataService.SaveUpdateExpense(formData).then((response) => {  
                        if(response.data.error=='100')
                        {
                            if(val=='A'){
                                vm.ShowAlert(vm.$t('ExpenseAdd',[vm.$t('Expense')]), "success", true,  vm.$t("Alert"));
                                this.onCancel();
                            }
                            else{
                                vm.ShowAlert(vm.$t('ExpenseAdd',[vm.$t('Expense')]), "success", true,  vm.$t("Alert"));
                                this.onCancel();
                            }
                        }
                        
                    });
                }
            });
        },
        CloseExpenseTrip: function () {
            this.ShowTripModel = false;
        },
        CloseExpenseTag: function () {
            this.ShowTagModel = false;
        },
        GetProjectDDL:async function () {
            var vm = this;
            vm.isLoading=true;
            var params = `associated=no`
           await ExpenseDataService.ExpenseGetProjectListing(params).then(function (response) {
            vm.isLoading=false;
                vm.ProjectData= response.data;
            });
        },
        GetTripNameDDL:async function () {
            var vm = this;
           await ExpenseDataService.GettTripListingddl().then(function (response) {
                vm.TripData = response.data;                
            });
        },
        GetCategoryNameDDL: async function () {
            var vm = this;
           await ExpenseDataService.GettCategoryListingddl().then(function (response) {
                vm.CategoryDataddl = response.data;
            });
        },
        GetTaskDDL:async function (projectId) {
            var vm = this;
            vm.isLoading=true;
            var params = `projectId=${projectId}`;
            await ExpenseDataService.GetTaskListingddl(params).then(function (response) {
                vm.isLoading=false;
                vm.TaskDataddl = response.data;
            }).catch(function (error) {
                vm.isLoading=false;
                console.log(error);
            });
        },
        GetCurrencyNameDDL:async function () {
            var vm = this;
           await ExpenseDataService.GetCurrencyNameddl().then(function (response) {
                vm.CurrencyNameddl = response.data;
            }).catch(function (error) {
                console.log(error);
            });
        },
        GetTagNameDDL:async function () {
            var vm = this;
            var params = `moduleName=Expenses`;
            await ExpenseDataService.GetTagNameddl(params).then(function (response) {
                vm.TagDataddl = response.data;
            });
        },
        GetCurrencyCode:async function (projectId) {
            var vm = this;
            vm.isLoading=true;
            var params = `projectId=${projectId}`;
           await ExpenseDataService.GetCurrencyCode(params).then(function (response) {  
                vm.isLoading=false;              
                vm.CurrencyCode=response.data;
            }).catch(function (error) {
                vm.isLoading=false;
                console.log(error);
            });;
        },
        OnChangeCurrency:function(){
            let currencyObj=this.CurrencyNameddl.find(x=>x.CurrencyId==this.Expense.CurrencyId);
            if(currencyObj){
                let currencyCodeStartIndex=currencyObj.Currency.indexOf("(");
                let currencyCodeEndIndex=currencyObj.Currency.indexOf(")", parseInt(currencyCodeStartIndex));
                var currencyCodeResult = currencyObj.Currency.substring(parseInt(currencyCodeStartIndex), parseInt(currencyCodeEndIndex) + 1);
                this.CurrencyCode=currencyCodeResult;
                if(this.CurrencyCode==''){
                    this.CurrencyCode=this.Currency;
                }
            }
        }
    }
}
</script>